$(document).ready(function () {
  AOS.init();
  // -----------------  Маска для телефона ----------------------------
  $(".form-phone").mask("+375 (999) 999 999", );
  // ------------------------------------------------------------------
  var lastScrollTop = 0;
  $(window).bind('scroll', function () {
    var menu = 50;
    if ($(window).scrollTop() > menu) {
      $('.header').addClass('scroll');
      $('.catalog').removeClass('active');
      $('.banner__body').addClass('scroll');
    } else if (($(window).scrollTop() == 0)) {
      $('.header').removeClass('scroll');
      $('.banner__body').removeClass('scroll');
      if ($('.header').hasClass('active')) {
        $('.catalog').addClass('active');
      }

    }
    var st = $(this).scrollTop();

    if (st > lastScrollTop) {
      $('.header').removeClass('up');
    } else {
      // upscroll code
      $('.header').addClass('up');
    }
    lastScrollTop = st;
  });


  $(document).mouseup(function (e) { // событие клика по веб-документу

    var sel = $('.select.is-active');
    if (!sel.is(e.target) // если клик был не по нашему блоку
      &&
      sel.has(e.target).length === 0) { // и не по его дочерним элементам
      sel.removeClass('is-active'); // скрываем его
    }
  });
  $(document).mouseup(function (e) { // событие клика по веб-документу
    var div = $(".catalog"); // тут указываем ID элемента
    var sel = $('.select');
    if (!div.is(e.target) // если клик был не по нашему блоку
      &&
      div.has(e.target).length === 0) { // и не по его дочерним элементам
      div.removeClass('active'); // скрываем его
    }
  });
  // -----------------  Счетчик цифр анимированный-----------------------
  var s;
  $({
    countNum: $('.partner__sum').text()
  }).animate({
    countNum: 554652
  }, {
    duration: 2500,
    easing: 'linear',
    step: function () {
      var s = Math.floor(this.countNum).toString().replace(/(\d)(?=(\d{3})+(\D|$))/g, '$1 ');
      $('.partner__sum').text(s);
    },
    complete: function () {
      $('.partner__sum').text(s);

    }
  });
  // -----------------------------------------------------------------------------------------

});

