// -----------------  Селект  --------------------

// переменная не переназначается, поэтому используем const
// используем querySelectorAll, чтобы собрать массив со всеми сущностями .select
const select = document.querySelectorAll(".select");

// если массив не пустой, пробегаемся в цикле по каждой найденой сущности
const sel_mas = document.querySelectorAll('.select__item');
for (let i = 0; i < sel_mas.length; i++) {
  sel_mas[i].addEventListener('click', function () {
    for (let j = 0; j < sel_mas.length; j++)
      sel_mas[j].classList.remove('active');
  })
}
if (select.length) {
  select.forEach((item) => {
    // достаем из текущей сущности .select__current
    const selectCurrent = item.querySelector(".select__current");

    item.addEventListener("click", (event) => {

      const el = event.target.dataset.choice;
      const text = event.target.innerText;
      event.target.classList.add('active');
      // Проверяем является ли это choosen и не выбрано ли его значение уже
      if (el === "choosen" && selectCurrent.innerText !== text) {
        selectCurrent.innerText = text;
      }
      item.classList.toggle("is-active");

    });
  });
}

// ---------------- Счетчик ---------------------
const quantities = document.querySelectorAll('.quantity');

[...quantities].forEach(function (quantity) {
  const minusButton = quantity.querySelector('.minus-btn');
  const plusButton = quantity.querySelector('.plus-btn');
  const inputField = quantity.querySelector('.input-btn');

  minusButton.addEventListener('click', function minusProduct() {
    const currentValue = Number(inputField.value);
    if (currentValue > 1) {
      inputField.value = currentValue - 1;
    } else inputField.value = 1
  });

  plusButton.addEventListener('click', function plusProduct() {
    const currentValue = Number(inputField.value);
    inputField.value = currentValue + 1;
  });

});

